<template>
  <swiper
    :style="{
      '--swiper-navigation-color': '#000',
      '--swiper-pagination-color': '#000',
      'border-radius':'50px',
      'margin-top':'80px',
      height: 'auto',
    }"
        :slidesPerView="3"
    :spaceBetween="30"

    :pagination="{
      clickable: true,
    }"
    :breakpoints="{
      '200': {
        slidesPerView: 1,
        spaceBetween: 20,
      },
      '768': {
        slidesPerView: 2,
        spaceBetween: 40,
      },
      '1024': {
        slidesPerView: 3,
        spaceBetween: 50,
      },
    }"
    :autoplay="{
      delay: 15000,
      disableOnInteraction: false,
    }"
    :navigation="false"
    :modules="modules"
    class="mySwiper"
  >
    <swiper-slide 
    v-for="product in category == '' ? products : $store.getters.getPorudctsCategory(category)"
    :key="product._id"
    class="col-lg-4 col-md-6 bg-white"
    @mouseover="HOVER_PRODUCT_BUTTONS('.product-buttons-' + product._id)"
    @mouseleave="UNHOVER_PRODUCT_BUTTONS('.product-buttons-' + product._id)"
    >
    <div class="mb-5 p-5 product border position-relative">
        <router-link
        @click="setProduct(product)"
        :to="{
          name: 'product',
          params: {
            id: product._id,
          },
        }"
      > 
      <div class="text-center product-imgs">
        <img
          :src="product.images[0]"
          class="visible-img"
          @mouseover="HOVER_IMG($event)"
          @mouseleave="UNHOVER_IMG($event)"
        />
        <img :src="product.images[2]" class="hidden-img" />
    </div>
      </router-link>
    <div
      :class="
        'product-buttons d-flex flex-column position-absolute product-buttons-' +
        product._id
      "
    >
      <button class="border rounded-circle bg-white d-inline-block p-2 text-center" @click="productView(product)">
        <i class="fa fa-search fs-6"></i>
      </button>
      <button class="border rounded-circle bg-white d-inline-block p-2 text-center">
        <i class="fa fa-heart fs-6"></i>
      </button>
      <button
        class="border rounded-circle fs-6 shopping-cart bg-white d-inline-block p-2 text-center fa fa-shopping-cart"
        @click="product_to_cart(product,$event)"
      >
        <i class="fa fa-check d-none fs-6"></i>
      </button>
    </div>
    <div class="product-details mt-5">
      <h5>{{ product.title }}</h5>
      <p>{{ substrDisc(product.description) }}</p>
      <p class="font-weight-bold fs-3 text-success">
        {{ product.price.length > 6 ? product.price : product.price
        }}<span class="fs-5">$</span>
      </p>
    </div>
    </div>
    </swiper-slide>
  </swiper>
  <div>

  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import VueToastify from "vue-toastify";
import { Swiper, SwiperSlide } from "swiper/vue";

import { Autoplay, EffectFade, Pagination, Navigation } from "swiper";

export default {
  name: "products-item",
  props:["category"],
  computed: {
    ...mapState(["products"]),
  },
  mounted() {
    this.$store.dispatch("getProducts");
  },
  methods: {
    productView(product){
      document.querySelector(".product-view").classList.add("open")
      this.$store.dispatch("getProductView", product);
    },
    setProduct(product){
      this.$store.dispatch("setProductView", product);
    },
    product_to_cart(product,e){
        e.target.classList.remove("fa-shopping-cart")
        e.target.children[0].classList.remove("d-none")
        setTimeout(() => {
          e.target.classList.add("fa-shopping-cart")
          e.target.children[0].classList.add("d-none")
        }, 1000);
      this.$store.dispatch("addTocart", {product,quantity:1});
    },
    substrDisc(disc) {return disc.substr(0, 54) + "...";},
    HOVER_IMG(el) {el.target.nextElementSibling.style.opacity = "1";},
    UNHOVER_IMG(el) {el.target.nextElementSibling.style.opacity = "0";},
    HOVER_PRODUCT_BUTTONS(e) {document.querySelector(e).classList.add("show")},
    UNHOVER_PRODUCT_BUTTONS(e) {document.querySelector(e).classList.remove("show")},
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [Autoplay, EffectFade, Pagination, Navigation],
    };
  },
};
</script>
