<template>
    <div class="nav-container fixed-top mb-3 bg-white py-3 shadow-sm">
        <div class="container">
            <div
                class="navbar row text-center align-items-center justify-content-center"
            >
                <div class="col-lg-4 text-start col-6 ecommerce-image">
                    {{ logoFunction() }}
                    <img :src="logoMain" />
                </div>
                <ul class="col-lg-4 col-6 list-unstyled text-center justify-content-center d-flex m-0 navbar-items">
                    <li class="position-absolute close-icon">
                        <i
                            class="fa fa-times text-light"
                            @click="closeMenu()"
                        ></i>
                    </li>
                    <li v-for="item in navItem" :key="item.id">
                        <router-link
                            :to="'/' + item.route"
                            class="text-decoration-none"
                            >{{ item.name }}</router-link
                        >
                    </li>
                </ul>
                <div
                    class="col-lg-4 col-6 navbar-right-details d-flex justify-content-end"
                >
                    <div class="cart position-relative" @click="open_cart()">
                    <p v-if="$store.state.cart.length"  class="bg-primary text-light rounded-circle cart-length"><span>{{ $store.state.cart.length }}</span></p>
                        <img :src="shoppingCart" class="svg-icons" />
                    </div>
                    <div><i class="fa fa-search search-icon" @click="openSearchBar()"></i></div>
                    <!-- <div class="favorite">
                        <img :src="heartUnFull" class="svg-icons" />
                    </div> -->
                    <div class="user-login">
                        <p class="m-0 d-flex" v-if="$store.state.currentUser">hello {{ $store.state.currentUser.username }}! <i @click="$store.dispatch('logout')" class="fa fa-arrow-right"></i></p>
                        <router-link v-if="!$store.state.currentUser"
                                :to="'/login'"
                                :class="'text-decoration-none text-dark'"
                                > 
                            <img :src="loginSvg" class="svg-icons" />
                            login
                        </router-link>
                    </div>
                    <div class="menu-icon-toggle d-none" @click="toggleNav()">
                        <i class="fa fa-bars"></i>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <router-view />
</template>

<script>
import navItem from "../json/navbar.json";
import logoDark from "../assets/logo_dark.png";
import shoppingCart from "../assets/svg/shopping-cart-outline-svgrepo-com.svg";
import heartFull from "../assets/svg/heart-full.svg";
import heartUnFull from "../assets/svg/heart-unfull.svg";
import loginSvg from "../assets/svg/login-svgrepo-com.svg";
import logoWhite from "../assets/logo_white.png";

export default {
    name: "navbar-component",
    logoMain: null,
    data: function () {
        return {
            navItem: navItem,
            logoDark: logoDark,
            logoWhite: logoWhite,
            shoppingCart: shoppingCart,
            heartFull: heartFull,
            loginSvg: loginSvg,
            heartUnFull: heartUnFull,
        };
    },
    methods: {
        logoFunction() {
            let mediaQuery = window.matchMedia("(max-width: 992px)");
            if (mediaQuery.matches) {
                this.logoMain = this.logoDark;
            } else {
                this.logoMain = this.logoDark;
            }
        },
        toggleNav() {
            document.querySelector(".navbar-items").classList.add("active");
        },
        closeMenu() {
            document.querySelector(".navbar-items").classList.remove("active");
        },
        open_cart(){
            document.querySelector(".cart-parent-div").classList.toggle("open-cart")
            document.querySelector(".cart-div").classList.toggle("open-cart")
        },
        openSearchBar(){
            document.querySelector(".search-bar-container").classList.toggle("open")   
        }
    },
};
</script>
