<template>
    <div :style="'margin-top:80px'">
        <checkoutComponent :name="'checkout-element'"></checkoutComponent>
        <h1 v-if="!cart.length" class="text-center mt-5">NO PRODUCTS IN THE CART. </h1>
        <!-- Button trigger modal -->
        <div class="container mt-5" v-if="cart.length">
            <div class="container">
                <form action="#" @submit="$event.preventDefault(),makeorder()">
                    <div class="row">
                        <div class="col-lg-6">
                            <div class="row">
                                <div class="col-lg-6 my-3">
                                    <label for="fname">First name *</label>
                                    <input type="text" v-model="fname" class="form-control" name="fname" required id="fname">
                                </div>
                                <div class="col-lg-6 my-3">
                                    <label for="lname">Last name *</label>
                                    <input type="text" v-model="lname" class="form-control" name="lname" required id="lname">
                                </div>
                                <div class="col-12 my-3">
                                    <label for="company">Company name (optional)</label>
                                    <input type="text" v-model="company" class="form-control" name="company" id="company">
                                </div>
                                <div class="col-12 my-3">
                                    <label for="contury">Country / Region *</label>
                                    <select name="contury" v-model="country" required id="contury" class="form-control">
                                        <option selected disabled>Select</option>
                                        <option>Egypt</option>
                                        <option>USA</option>
                                    </select>
                                </div>
                                <div class="col-lg-12 my-3">
                                    <label for="street">Street address *</label>
                                    <input type="text" v-model="street" class="form-control" name="street" required id="street">
                                </div>
                                <div class="col-lg-12 my-3">
                                    <label for="phone">Phone *</label>
                                    <input type="number" v-model="phone" class="form-control" name="phone" required id="phone">
                                </div>
                                <div class="col-lg-12 my-3">
                                    <label for="email">Email address  *</label>
                                    <input type="email" v-model="email" class="form-control" name="email" required id="email">
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-6">
                            <div class="subtotal-container border p-4">
                                <p class="border-bottom fw-bold pb-2">Your order</p>
                                <div class="d-flex justify-content-between fs-6 py-3"><span>Product</span><span>Price</span></div>
                                <div class="d-flex justify-content-between my-4" v-for="product in cart" :key="product.id">
                                    <h6>{{ product.product.title }} <span :style="'font-size:10px'" class="fw-bold">x</span> {{ product.quantity }} </h6>
                                    <h6>${{ product.product.price }}</h6>
                                </div>
                                <div class="d-flex justify-content-between my-4 pt-4 border-top">
                                    <h5>TOTAL</h5>
                                    <h5>${{ $store.getters.getCartPrices }}</h5>
                                </div>
                                <button class="btn fw-bold btn-success w-100" type="submit" v-if="$store.state.currentUser">Make Order</button>
                                <router-link v-if="!$store.state.currentUser"
                                        :to="'/login'"
                                        > 
                                    <button class="btn fw-bold btn-danger w-100" type="submit">Please login first!</button>
                                </router-link>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>

</template>
<script>
import { mapState } from "vuex";
import checkoutComponent from "../components/checkout.vue";
export default {
    name:"checkout",
    data(){
        return{
            fname:"",
            lname:"",
            company:"",
            country:"",
            street:"",
            phone:"",
            email:"",
        }
    },
    computed: {
        ...mapState(["cart"])
    },
    components:{
        checkoutComponent,
    },
    methods:{
        makeorder(){
            this.$router.push({path: '/orderdetails', });
        }
    }
}
</script>