<template>
    <product_viewVue></product_viewVue>
    <div class="container" :style="'margin-top:130px'">
        <div class="row">
            <div class="col-lg-6 mb-5">
                  <div class="border p-5 rounded">
                    <div>
                        <swiper
                        ref="swiperTop"
                        :style="{
                        '--swiper-navigation-color': '#000',
                        '--swiper-pagination-color': '#000',
                        }"
                        :slidesPerView="1"
                        :spaceBetween="500"
                        :initialSlide="0"
                        :runCallbacksOnInit="true"
                        @slideChange="onSlide"
                        :slideToClickedSlide="true"
                        :pagination="{
                        clickable: true,
                        }"
                        :navigation="true"
                        :autoplay="{
                        delay: 5555000,
                        disableOnInteraction: true,
                        }"
                        :modules="modules"
                        class="mySwiper swiper-products"
                    >
                        <swiper-slide v-for="(image,i) in product_page.images" :key="i" class="text-center swiper-slide-product-page" :id="'swiper_'+product_page._id">
                            <img :src="image"/>
                        </swiper-slide>
                    </swiper>
                    </div>
                        <div class="text-center product-images-parent mt-5">
                        <swiper
                            :slidesPerView="4"
                            :spaceBetween="20"
                            :autoplay="{
                            delay: 2000,
                            disableOnInteraction: false,
                            }"
                            :modules="modules"
                            class="mySwiper"
                        >
                        </swiper>
                    </div>
                  </div>
            </div>
            <div class="col-lg-6 mb-5 product-page-details mt-4">
                <h3 class="fw-bold">{{ product_page.title }}</h3>
                <p class="fs-6">By {{ product_page.company }}</p>
                <p class="fw-bold product-page-price text-secondary my-3">${{ product_page.price }}</p>
                <p class="product-page-description">{{ product_page.description }}</p>
                <button class="btn btn-success rounded-pill mt-3 p-3 w-100" @click="product_to_cart(product_page)"><i class="fa fa-plus"></i> ADD TO CART</button>
            </div>
            <div class="col-lg-12 mt-5">
                <button class="specifications btn btn-danger rounded-0 py-2 fw-bold fs-5 px-5">specifications</button>
                <!-- <div class="border-bottom mb-5 mt-3"></div> -->
                <ul class="bg-light p-5">
                    <li v-for="specification in product_page.specifications" :key="specification.id" class="mb-5 fs-6">{{ specification }}</li>
                </ul>
            </div>
            <div class="col-lg-12 mt-5">
                <h1 class="text-center fw-bold">Related products</h1>
                <products :category="product_page.category"></products>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Autoplay, EffectFade, Pagination, Navigation } from "swiper";
import { VueImageZoomer } from 'vue-image-zoomer';
import products from "@/components/products.vue";
import product_viewVue from "@/components/product_view.vue";

export default {
    name:"product",
    data(){
        return{
            currentIndex: 1
        }
    },
    computed: {
        ...mapState(["product_page"]),
    },
    mounted(){
        this.$store.dispatch("productPage", this.$route.params.id)
    },
    methods:{
        toSlide(i) {
                // this.$refs.swiperTop.initialSlide = i
                // // this.$refs.swiperTop.swiper.slideTo(i, 0);
                console.log(i)
        },
        product_to_cart(product){
            this.$store.dispatch("addTocart", {product,quantity:1});
        },
    },
    components: {
        Swiper,
        SwiperSlide,
        products,
        product_viewVue,
        VueImageZoomer,
    },
    setup() {
        return {
            modules: [Autoplay, EffectFade, Pagination, Navigation],
        };
    },
}
</script>