<template>
    <div class="container" :style="'margin-top:150px'">
        <div class="row">
        <div class="col-lg-6 border-end">
            <form class="register col-lg-11" v-if="show" action="#" @submit="$event.preventDefault(),register()">
                <h3 class="fw-bold">REGISTER</h3>
                <div class="alert alert-danger err d-none"></div>
                <div class="alert alert-success success d-none"></div>
                <div class="form-group my-4">
                    <label for="name">User Name *</label>
                    <input type="text" id="name" v-model="name" class="form-control" required>
                </div>
                <div class="form-group my-4">
                    <label for="email">Email address *</label>
                    <input type="email" id="email" v-model="email" class="form-control" required>
                </div>
                <div class="form-group my-4">
                    <label for="password">Password *</label>
                    <input type="password" id="password" v-model="password" class="form-control" required>
                </div>
                <button class="btn btn-success w-100" type="subimt">Regsiter</button>
            </form>
            <form class="login col-lg-11" v-if="!show" action="#" @submit="$event.preventDefault(),login()">
                <h3 class="fw-bold">LOGIN</h3>
                <div class="alert alert-danger err-login d-none"></div>
                <div class="alert alert-success success-login d-none"></div>
                <div class="form-group my-4">
                    <label for="lemail">Email address *</label>
                    <input type="email" id="lemail" v-model="lemail" class="form-control" required>
                </div>
                <div class="form-group my-4">
                    <label for="lpassword">Password *</label>
                    <input type="password" id="lpassword" v-model="lpassword" class="form-control" required>
                </div>
                <button class="btn btn-success w-100">LOG IN</button>
            </form>
        </div>
        <div class="col-lg-6 mt-5">
            <div class="col-lg-11 m-auto text-center">
                <h3 v-if="!show">LOGIN</h3>
                <h3 v-if="show">REGISTER</h3>
                <p>
                    Registering for this site allows you to access your order status and history. Just fill in the fields below, and  get a new account set up for you in no time. We will only ask you for information necessary to make the purchase process faster and easier.
                </p>
                <button class="btn btn-light rounded-0 px-3" v-if="!show" @click="registerShow()">Register</button>
                <button class="btn btn-light rounded-0 px-3" v-if="show" @click="loginShow()">Login</button>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

export default {
    data(){
        return{
            show:true,
            name:'',
            password: '',
            email: '',
            lemail:"",
            lpassword:""
        }
    },
    name:"login",
    computed: {
        ...mapState(["login,currentuser"])
    },
    methods:{
        registerShow(){
            this.show = true
        },
        loginShow(){
            this.show = false
        },
        register(){
            let user = {user:{username:this.name,password:this.password,email:this.email}}
            this.$store.dispatch("loginUser",user)
        },
        login(){
            let user = {password:this.lpassword,email:this.lemail}
            this.$store.dispatch("currentUser",user)
        }
    },
    mounted(){
        if(this.$store.state.currentUser){
           this.$router.push({path: '/', });
        }
    }
}
</script>