<template>
    <div class="fixed-top cart-parent-div h-100 w-100" @click="close_cart($event)">
        <div class="bg-white cart-div h-100 p-3 position-relative">
            <i class="fa fa-times times-cart position-absolute" @click="close_cart_times($event)"></i>
            <i class="fa fa-trash text-danger products-trash fs-6" @click="this.$store.dispatch('removeProductsCart')" v-if="cart.length"></i>
            <div class="cart-content">
                <div v-if="!cart.length" class="text-center fs-5 mt-5">
                    No products in the cart.
                    <button class="btn btn-primary px-5 mt-3 rounded-pill">Return To Shop</button>
                </div>
                <div v-for="product in cart" :key="product.id" class="d-flex align-items-center bg-white p-2 rounded py-4 border-bottom product-cart position-relative">
                    <div class="product-cart-img">
                        <img :src="product.product.images[0]" alt="">
                    </div>
                    <div class="product-cart-title">
                        <p class="m-0">{{ product.product.title }}</p>
                        <div class="text-dark mt-3">{{ product.quantity }} x {{ parseInt(product.product.price) * product.quantity }}$</div>
                    </div>
                    <div class="text-danger trash-product-cart"><i class="fa fa-trash trash-product" @click="removeCart(product)"></i></div>
                </div>
            </div>
            <div class="cart-buttons position-absolute" v-if="cart.length">
                <p class="d-flex justify-content-between" :style="'font-size:12px'"><span>SUBTOTAL:</span><span>${{ $store.getters.getCartPrices }}</span></p>
                <router-link
                    :to="'/cart'"
                    @click="close_cart_times()"
                    > 
                <button class="btn btn-primary rounded-pill shadow-sm fw-bold w-100">View cart</button>
              </router-link>
                <router-link
                    :to="'/checkout'"
                    @click="close_cart_times()"
                    > 
                <button class="btn btn-light rounded-pill mt-3 shadow-sm fw-bold w-100">Checkout</button>
              </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "cart",
    computed: {
        ...mapState(["cart"])
    },
    methods:{
        close_cart(e){
            if(e.target.classList.contains("cart-parent-div")){
                document.querySelector(".cart-div").classList.remove("open-cart")
                document.querySelector(".cart-parent-div").classList.remove("open-cart")
            }
        },
        close_cart_times(e){
            document.querySelector(".cart-div").classList.remove("open-cart")
            document.querySelector(".cart-parent-div").classList.remove("open-cart")
        },
        removeCart(product){
            this.$store.dispatch("removeFromCart", product);
        }
    }
}
</script>