<template>
    <div class="fixed-top w-100 h-100 bg-white p-5 overflow-scroll search-bar-container shadow-lg">
        <p class="text-center">What are you looking for?</p>
        <i class="fa fa-times position-absolute end-0 fs-4 close-search-bar" @click="closeSearchBar()"></i>
        <div class="input-searching position-relative"><input type="text" placeholder="Search for..." class="custom-input p-3 w-100 border-bottom" v-model.trim="search"><i class="fa fa-search position-absolute fs-5"></i></div>
        <div class="searching-box">
            <router-link v-if="search" class="text-decoration-none text-dark d-flex searched-product align-items-center p-3" v-for="(product,i) in productsSearch()" :key="i" :to="`/product/${product._id}`" @click="setProduct(product)">
                <div class="product-img-search"><img :src="product.images[0]" alt="" class="img-fluid"></div>
                <div class="ms-2">
                    <p class="m-0">{{ product.title }}</p>
                    <p class="m-0 text-danger">${{ product.price }}</p>
                </div>
            </router-link>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

export default {
    name:"search",
    data(){
        return{
            search:'',
            test:''
        }
    },
    computed: {
        ...mapState(["products"])
    },
    methods:{
        productsSearch(){
            return this.products.filter(el=>{
                if(el.title.toLowerCase().includes(this.search) || el.title.toUpperCase().includes(this.search)){
                    return el.title.toLowerCase().includes(this.search) || el.title.toUpperCase().includes(this.search)
                }
                 
            })
        },
        closeSearchBar(){
            document.querySelector(".search-bar-container").classList.toggle("open")   
        },
        setProduct(product){
            this.$store.dispatch("setProductView", product);
            document.querySelector(".search-bar-container").classList.toggle("open")   
        },
    }
}
</script>