<template>
    <div class="product-view fixed-top w-100 h-100" @click="$event.target.classList.remove('open')">
        <div class="product-view-content bg-white shadow-md row">
              <div class="swiperview col-lg-6">
                <swiper
                    :style="{
                        height: '100%',
                    }"
                    :pagination="{
                        dynamicBullets: true,
                    }"
                    :autoplay="{
                    delay: 2500,
                    disableOnInteraction: false,
                    }"
                    :modules="modules"
                    class="mySwiper"
                >
                    <swiper-slide v-for="(image,i) in productView.images" :key="i" class="bg-light text-center d-flex flex-column justify-content-center align-items-center">
                        <img :src="image" class="img-fluid" :style="'height:500px'"/>
                    </swiper-slide>
                </swiper>
              </div>
              <div class="col-lg-6 py-5 d-flex flex-column justify-content-between">
                <div>
                    <div class="product-name-view"><h4 class="fw-bold">{{ productView.title }}</h4></div>
                    <div class="product-price-view"><p class="fs-3 text-success">{{ productView.price }}$</p></div>
                    <div class="product-description-view my-4" :style="'height:350px;overflow:scroll'">
                        <ul v-for="(specification,i) in productView.specifications" :key="i" class="fs-6">
                            <li class="pb-3 border-bottom">{{ specification }}</li>
                        </ul>
                    </div>
                </div>
                <div class="row">
                    <div class="col-9"><button class="btn btn-success rounded-0 p-3 w-100" @click="product_to_cart(productView)">ADD TO CART</button></div>
                    <div class="col-3"><button class="btn btn-danger text-light rounded-0 w-100 h-100"><i class="fa fa-heart"></i></button></div>
                </div>
              </div>
        </div>
    </div>
</template>
<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Autoplay, EffectFade, Pagination, Navigation } from "swiper";
import { mapActions, mapGetters, mapState } from "vuex";

export default {
    name:"productView",
    components: {
        Swiper,
        SwiperSlide,
    },
    methods:{
        product_to_cart(product){
            this.$store.dispatch("addTocart", {product,quantity:1});
        },
    },
    computed: {
        ...mapState(["productView"]),
    },
    setup() {
        return {
        modules: [Pagination,Autoplay],
        };
    },
}
</script>