import { createStore } from "vuex";
import axios from "axios";
import createPersistedState from 'vuex-persistedstate'
// import categories from "@/json/categories.json"
const state = {
  name: "ahmed",
  products: "",
  cart: [],
  mobiles: [],
  categories: [],
  productView: [],
  product_page: [],
  current_params: [],
  login: [],
  currentuser: ["none"],
  logined: false,
};

const mutations = {
  LOGIN(state, {user},mess) {
    let userInList = state.login.find((item) =>{
      return item.user.email === user.email && item.user.username === user.username
    })

    if(userInList){
      document.querySelector(".err").classList.remove("d-none");
      document.querySelector(".success").classList.add("d-none");
      document.querySelector(".err").innerHTML = "لقد تم التسجيل بهذا الحساب من قبل"
      return;
    }else{
      document.querySelector(".success").classList.remove("d-none");
      document.querySelector(".err").classList.add("d-none");
      document.querySelector(".success").innerHTML = "لقد تم التسجيل بنجاح قم بالتسجيل الان";

    }
    state.login.push({user});
  },
  GET_PRODUCTS(state, products) {
    state.products = products;
  },
  LOGOUT_USER(state) {
    state.currentUser = "";
  },
  CURRENT_USER(state, user) {
    let userInList = state.login.find((item) =>{
      return item.user.email === user.email && item.user.password === user.password
    })
    if(userInList){
      state.currentUser = userInList.user;
      document.querySelector(".err-login").classList.add("d-none");
      document.querySelector(".success-login").classList.remove("d-none");
      document.querySelector(".success-login").innerHTML = "لقد تم التسجيل بنجاح";
      state.logined = true
    }else{
      document.querySelector(".err-login").classList.remove("d-none");
      document.querySelector(".success-login").classList.add("d-none");
      document.querySelector(".err-login").innerHTML = "البريد الالكتروني او الباسورد خطأ الرجاء التحقق"
    }
  },
  CURRENT_PARAMS(state, params) {
    state.current_params = params;
  },
  ADD_CART(state, {product , quantity}) {

    let productInCart = state.cart.find((item) =>{
      return item.product._id === product._id
    })

    if(productInCart){
      productInCart.quantity += quantity;
      return;
    }

    state.cart.push({
      product,
      quantity
    });
    localStorage.setItem("products", JSON.stringify(state.cart));
  },
  REMOVE_FROM_CART(state,product){
    let removedEl = state.cart.find(el=> el.product._id == product._id)
    state.cart.splice(state.cart.indexOf(removedEl), 1)
  },
  GET_CATEGORIES(state,category){
    state.categories = category
  },
  REMOVE_PRODUCTS_FROM_CART(state){
    state.cart.length = 0
  },
  GET_PRODUCT_VIEW(state, product){
    state.productView = product;
  },
  GET_PRODUCT_PAGE(state, product){
    state.productView = product;
  },
  SET_PRODUCT_VIEW(state, product){
    state.product_page = product
  },
};

const actions = {
  logout({ commit }){
    commit("LOGOUT_USER")
  },
  loginUser({ commit }, {user},mess){
    commit("LOGIN",{user},mess)
  },
  currentUser({ commit }, user){
    commit("CURRENT_USER",user)
  },
  getProducts({ commit }) {
    axios
      .get("https://electronic-shop-api.herokuapp.com/product/all")
      .then((response) => {
        if(response.data){
          commit("GET_PRODUCTS", response.data)
        }
      });
  },
  getCategories({commit}){
    axios
    .get("https://electronic-shop-api.herokuapp.com/category")
    .then((response) => {
      if(response.data){
        commit("GET_CATEGORIES", response.data)
      }
    });
  },
  addTocart({ commit }, {product, quantity}) {
    commit("ADD_CART", {product, quantity});
  },
  removeFromCart({ commit }, {product}){
    commit("REMOVE_FROM_CART",product)
  },
  removeProductsCart({ commit }){
    commit("REMOVE_PRODUCTS_FROM_CART")
  },
  getProductView({ commit }, product){
    commit("GET_PRODUCT_VIEW",product)
  },
  currentParams({ commit }, params){
    commit("CURRENT_PARAMS",params)
  },
  productPage({ commit }, id){
    axios
    .get(`https://electronic-shop-api.herokuapp.com/product/${id}`)
    .then((response) => {
      if(response.data){
        commit("GET_PRODUCT_PAGE", response.data)
      }
    });
  },
  setProductView({ commit }, product){
    commit("SET_PRODUCT_VIEW",product)
  },
};

const getters = {
  getPorudctsCategory: (state) => (category) => {
    if(state.products.length > 1){
      return state.products.filter((el) => el.category == category);
    }
  },
  getCartPrices: (state) =>{
    let total = 0
    state.cart.map(product=> total += product.product.price * product.quantity)
    return parseInt(total)
  },
};

export default createStore({
  state, // computed
  getters, // computed
  mutations, // state تقوم بتغير البيانات داخل ال -> method
  actions, // method
  plugins: [createPersistedState()]
});
