<template>
    <div :style="'margin-top:80px'">
        <checkoutComponent :name="'cart-element'"></checkoutComponent>
        <h1 v-if="!cart.length" class="text-center mt-5">NO PRODUCTS IN THE CART. </h1>
        <div class="container mt-5" v-if="cart.length">
            <div class="row">
                <div class="products-cart-view col-lg-6">
                    <table class="table w-100">
                        <thead>
                            <tr>
                                <th scope="col">PRODUCT</th>
                                <th class="text-center" scope="col">PRICE</th>
                                <th class="text-center" scope="col">QUANTITY</th>
                                <th class="text-center" scope="col">SUBTOTAL</th>
                                <th class="text-center" scope="col">REMOVE</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="product in cart" :key="product.id">
                                <td scope="row"><img :src="product.product.images[0]" width="30" height="30"/> {{ product.product.title }} </td>
                                <td class="text-center">${{ product.product.price }}</td>
                                <td class="text-center">{{ product.quantity }}</td>
                                <td class="text-center">${{ product.product.price * product.quantity }}</td>
                                <td class="text-center"><i class="fa fa-trash text-danger" @click="removeCart(product)" :style="'cursor:pointer'"></i></td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="col-lg-6">
                    <div class="subtotal-container border p-4">
                        <p class="border-bottom fw-bold pb-2">CART TOTALS</p>
                        <div class="d-flex justify-content-between my-4">
                            <h5>TOTAL</h5>
                            <h5>${{ $store.getters.getCartPrices }}</h5>
                        </div>
                        <router-link
                                :to="'/checkout'"
                                > 
                            <button class="btn fw-bold btn-primary w-100 rounded-pill mb-3">Proceed to checkout</button>
                        </router-link>
                        <router-link
                                :to="'/'"
                                > 
                            <button class="btn fw-bold btn-light w-100 border rounded-pill">Continue shopping</button>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
import { mapState } from "vuex";
import checkoutComponent from "../components/checkout.vue";
export default {
    name:"cart",
    computed: {
        ...mapState(["cart"])
    },
    components:{
        checkoutComponent,
    },
    methods:{
        removeCart(product){
            this.$store.dispatch("removeFromCart", product);
        }
    }
}
</script>