<template>
   <div class="my-3 py-5 categories">
  <swiper
    :slidesPerView="3"
    :spaceBetween="30"
    :autoplay="{
      delay: 2000,
      disableOnInteraction: false,
    }"
        :breakpoints="{
      '200': {
        slidesPerView: 1,
        spaceBetween: 20,
      },
      '768': {
        slidesPerView: 2,
        spaceBetween: 40,
      },
      '1024': {
        slidesPerView: 3,
        spaceBetween: 50,
      },
    }"
    :modules="modules"
    class="mySwiper"
  >
    <swiper-slide v-for="(category,index) in categories.slice(0,-1)" :key="index" class="category">
        <div class="text-center rounded-lg shadow-sm p-5">
            <h4 class="fw-bold mb-5">{{category.name}} & {{category.icon}}</h4>
            <i :class="'fas fa-' + category.icon + ' mb-3 text-success'" style="font-size:5rem"></i>
        </div>
    </swiper-slide>
  </swiper>
    </div>
</template>
<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import { Autoplay, EffectFade, Pagination, Navigation } from "swiper";
import { mapState } from "vuex";

export default {
    name:"category",
    components: { Swiper, SwiperSlide },
    computed:{
        ...mapState(["categories"]),
    },
    mounted() {
        this.$store.dispatch("getCategories");
    },
    setup() {
        return {
            modules: [Autoplay, EffectFade, Pagination, Navigation],
        };
    },
}
</script>