<template>
    <div :style="'margin-top:80px'">
        <checkoutComponent :name="'orderdetails-element'"></checkoutComponent>
        <h1 v-if="!cart.length" class="text-center mt-5">NO PRODUCTS IN THE CART. </h1>
        <!-- Button trigger modal -->
        <div class="container mt-5" v-if="cart.length">
            <div class="container">
                <div class="text-center">
                    <p class="fs-6 text-danger">redircet in {{ timer }}</p>
                    <i class="fa fa-box fs-3 mb-3"></i>
                    <h4>THANK YOU. YOUR ORDER HAS BEEN RECEIVED.</h4>
                    <ul class="list-unstyled d-inline-block p-4 border border-opacity-75">
                        <li class="m-0 mb-2">Order number: 1</li>
                        <li class="m-0 mb-2">Total: ${{ $store.getters.getCartPrices }}</li>
                        <li class="m-0">Payment method: Check payments</li>
                    </ul>
                </div>
                <h3>Order details</h3>
                <div class="d-flex justify-content-between border-top border-bottom py-3 mb-3"><p class="m-0">PRODUCT</p><p class="m-0">PRICE</p></div>
                <div class="d-flex justify-content-between fs-6" v-for="product in cart" :key="product.id">
                    <p>{{ product.product.title }}</p>
                    <p>${{ product.product.price }}</p>
                </div>
                <div class="d-flex justify-content-between border-top py-3 mb-3 fs-6"><p class="m-0">TOTAL</p><p class="m-0">${{ $store.getters.getCartPrices }}</p></div>
            </div>
        </div>
    </div>

</template>
<script>
import { mapState } from "vuex";
import checkoutComponent from "../components/checkout.vue";
export default {
    name:"orderdetails",
    data(){
      return{
        timer:10,
      }  
    },
    computed: {
        ...mapState(["cart"])
    },
    components:{
        checkoutComponent,
    },
    mounted(){
        let interval = setInterval(() => {
            this.timer--
        }, 1000);
        setTimeout(() => {
            clearInterval(interval)
            this.$store.dispatch('removeProductsCart');
            this.$router.push({path: '/', });
        }, 10000);
    }
}
</script>